import React, {Component} from 'react';
import { ProgressBar } from 'react-bootstrap';

class Timer extends Component {
    constructor(props) {
        super(props);
        this.state = {
          timer: 15,
        };
      }

    componentDidMount() {
        this.interval = setInterval(() => this.setState({ timer: this.state.timer - 1 }), 1000);
        this.handleTimerChange(this.state.timer - 1);
    }
    componentWillUnmount() {
        clearInterval(this.interval);
    }
    componentDidUpdate() {
        if(this.state.timer === 0){
            // Out of time
            console.log("No time");
        }
    }
    handleTimerChange = (newTimer) => {
        this.props.handleTimerChange(newTimer)
    };

    render() {
        return (
            <ProgressBar animated variant="danger" now={(this.state.timer) * 5} label={`${this.state.timer}`}/>
        );
    }
}
   
export default Timer;

